/* Основные стили для хедера */
header {
  background-color: white;
  color: black;
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0rem 3rem;
  flex-wrap: wrap; /* Allows items to wrap to new lines if necessary */
}

/* Стили для логотипа */
.logo-container {
  margin-bottom: 2.5rem;
  display: flex;
  align-items: center;
}

.logo {
  height: 6rem; /* 60px */
}

/* Стили для навигации */
.navigation1 {
  flex-grow: 1;
  display: flex;
  justify-content: center;
}

.nav-menu {
  list-style: none;
  display: flex;
  flex-wrap: wrap; /* Allows menu items to wrap */
  justify-content: center;
  gap: 3rem; /* 45px */
  padding: 0;
  margin: 0;
}

.nav-menu li {
  margin: 0;
}

.nav-menu li button {
  background: none;
  border: none;
  color: #2c3e50;
  text-decoration: none;
  font-weight: 600;
  font-size: 1.5rem; /* 26px */
  transition: color 0.3s ease;
  position: relative;
  cursor: pointer;
  white-space: nowrap;
  letter-spacing: -0.05rem; /* -0.5px */
}

.nav-menu li button:hover {
  color: darkslategray;
}

.nav-menu li button::after {
  content: '';
  position: absolute;
  width: 0;
  height: 0.3rem; /* 3px */
  background: #2c3e50;
  left: 0;
  bottom: -0.5rem; /* -5px */
  transition: width 0.3s ease;
}

.nav-menu li button:hover::after {
  width: 100%;
}

/* Стили для кнопки */
.header-download-btn {
  display: flex;
  align-items: center;
}

.header-download-btn button {
  background-color: #41930b;
  color: white;
  border: none;
  padding: 1rem 1.2rem; /* 12px 25px */
  cursor: pointer;
  font-weight: 700;
  font-size: 1.2rem; /* 16px */
  border-radius: 0.5rem; /* 5px */
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.header-download-btn button:hover {
  background-color: #57d100;
  transform: scale(1.04);
}


@media (max-width: 576px) {
  .logo {
    height: 3rem; /* 60px */
  }
  .nav-menu {
    margin-top: 1rem;
    gap: 0.4rem; /* 5px */
  }

  .nav-menu li button {
    font-size: 1.2rem; /* 18px */
  }

  .header-download-btn button {
    font-size: 1.5rem; /* 14px */
    padding: 1rem 2rem; /* 10px 20px */
    margin-bottom: 1rem;
    margin-top: 1rem;
  }

  .logo-container {
    margin-bottom: 0px;
  }
}


@media (min-width: 1200px) and (max-width: 1600px) {
  .nav-menu {
    gap: 0.8rem; /* Отступы между элементами меню */
  }
  .nav-menu li button {
    font-size: 1.3rem; /* 18px */
  }
  .header-download-btn button {
    font-weight: 500;
    font-size: 1rem; /* 16px */
    border-radius: 0.5rem; /* 5px */
  }
}

/* Средние экраны */
@media (min-width: 577px) and (max-width: 1199px) {
  .header-container {
    flex-direction: column;
    align-items: center;
    padding: 1rem;
    gap: 0,5rem; /* Делаем отступы между строками */
  }

  .logo-container {
    order: 1; /* Логотип в первой строке */
    display: flex;
    justify-content: center;
    width: 100%;
  }

  .navigation1 {
    order: 2; /* Навигация во второй строке */
    display: flex;
    justify-content: center;
    width: 100%;
  }

  .nav-menu {
    flex-direction: row;
    flex-wrap: wrap; /* Позволяет элементам оборачиваться */
    justify-content: center;
    gap: 1rem; /* Отступы между элементами меню */
    width: 100%;
  }

  .header-download-btn {
    margin-top: 1rem;
    order: 3; /* Кнопка в третьей строке */
    display: flex;
    justify-content: center;
    width: 100%;
  }
}
